.button {
  background-color: #d5b5ff;
  color: #7804c6;
  border: 3px solid #bc66ff;
  border-radius: 10px;
  outline: none;
  padding: 10px;
  cursor: pointer;
  transition: 0.1s linear;
  align-self: center;
}
.button:hover {
  background-color: #fff;
  transform: scale(1.1);
  color: #7804c6;
}
